/* Add a background color to the top navigation */
.topnav {
    background-color: #5a819e;
    overflow: hidden;
  }
  
  /* Style the links inside the navigation bar */
  .topnav a {
    float: right;
    color: #ffffff;
    text-align: center;
    padding: 14px 40px;
    text-decoration: none;

    font-family: "Noto Sans JP";
    font-style: normal;
    font-weight: 540;
    font-size: 25px;
    line-height: 43px;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: #d35563;
    color: #ffffff;
  }
  
  /* Add a color to the active/current link */
  .topnav a.active {
    background-color: #4CAF50;
    color: white;
  }
  .avatar {
    vertical-align: middle;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 14px;
    position: absolute;
   
    left: 20px;
    top: -2px;
  }
  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
  }