table.customTable {
    position: absolute;
    width: 50%;
    height: 30%;
    left: 15%;
    top: 85%;

  background-color: #FFFFFF;
  border-collapse: collapse;
  border-width: 0px;
  border-color: #D35563;
  border-style: solid;
  color: #000000;
}

table.customTable td, table.customTable th {
    width: 10%;
  border-width: 0px;
  border-color: #D35563;
  border-style: solid;
  padding: 5px;
}

table.customTable thead {
  background-color: #FFFFFF;
  
}
